.Counter {
  width: 400px;
  padding: 10px;
  border: 1px solid black;
  text-align: center;
  margin: 200px auto;
}

.CounterControls > button {
  border: none;
  padding: 10px 15px;
  background-color: teal;
  color: #fff;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  transition: box-shadow 250ms linear;
}

.CounterValue {
  margin-right: 15px;
  margin-left: 15px;
  font-size: 64px;
  font-family: monospace;
  text-align: center;
}